.grid {
  font-family: "Lato", sans-serif;
}

.grid li {
  display: inline-block;
  width: 380px;
  margin: 0;
  padding: 15px;
  text-align: left;
  position: relative;
}
.grid figure {
  margin: 0;
  position: relative;
}

.grid figure img {
  max-width: 100%;
  display: block;
  position: relative;
}
.grid figcaption {
  position: absolute;
  /* width: 250px; */
  top: 0;
  left: 0;
  padding: 15px;

  background: linear-gradient(to bottom, #dcf5f3 0%, #aad0d2 100%);
  /* background: #cfeae8; */
  color: #f0473e;
  height: 300px;
}

.grid figcaption h3 {
  margin: 0;
  padding: 0;
  margin-top: 5px;
  color: #f0473e;
  font-weight: 800;
  margin-bottom: 5px;
  font-family: "Lato", sans-serif;
}

.grid figcaption span {
  margin-bottom: 20px;
  font-family: "Lato", sans-serif;
}

.grid figcaption p {
  font-size: 14px;
  text-align: left;
  margin-left: 10px;
  padding: 0;
  margin-top: 10px;
  color: #4b6064;
  padding-bottom: 10px;
  font-family: "Lato", sans-serif;
  /* font-weight: 300; */
}
.grid figcaption img {
  width: 45px;
  margin-right: 10px;
  margin-left: 10px;
  margin-bottom: 20px;
}

.grid figcaption label {
  position: relative;
  display: flex;
}

.grid figcaption span:before {
  content: "on ";
}

.grid figcaption a {
  font-size: 20px;
  display: inline;
  margin-right: 1rem;
  margin-left: 1rem;
  font-weight: 600;
  cursor: pointer;
  color: #333333;
  background: linear-gradient(to bottom, #f0473e 0%, #f0473e 100%);
  background-position: 0 100%;
  background-repeat: repeat-x;
  background-size: 4px 4px;
  text-decoration: none;
  transition: background-size 0.2s;
}

.grid figcaption a:hover {
  background-size: 4px 50px;
}

.no-touch .grid figure:hover figcaption,
.grid figure.cs-hover figcaption {
  opacity: 1;
  -webkit-transform: translate(15px, 15px);
  -moz-transform: translate(15px, 15px);
  -ms-transform: translate(15px, 15px);
  transform: translate(15px, 15px);
}

.grid figcaption {
  height: 100%;
  width: 100%;
  opacity: 0;
  text-align: center;
  backface-visibility: hidden;
  transition: transform 0.3s, opacity 0.3s;
}

/* Individual Caption Styles */

/* Caption Style 1 */
.grid figcaption {
  height: 100%;
  width: 100%;
  opacity: 0;
  text-align: center;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
  -moz-transition: -moz-transform 0.3s, opacity 0.3s;
  transition: transform 0.3s, opacity 0.3s;
}

.btn-close-figcaption {
  position: absolute;
  color: black;
  right: 1rem;
  top: 1rem;
  width: 15px;
  height: 15px;
  margin: 0px;
  padding: 0px;
  border: none;
  background: transparent;
  cursor: pointer;
  outline: none;
}

.touch .grid figure:hover figcaption,
.grid figure.cs-hover figcaption,
.grid figure:active figcaption,
.grid figure:disabled figcaption,
.grid figure.cs-active figcaption {
  opacity: 1;
  -webkit-transform: translate(15px, 15px);
  -moz-transform: translate(15px, 15px);
  -ms-transform: translate(15px, 15px);
  transform: translate(15px, 15px);
}

