.grid figcaption span {
  display: block;
}

.grid figcaption a {
  margin-top: 10px;
}

@media (min-width: 320px) and (max-width: 768px) {
  .grid {
    padding: 10px 10px 100px 10px;
  }

  .grid li {
    display: inline-block;
    width: 350px;
    height: 350px;
    margin: 0;
    padding: 0;
    text-align: left;
    position: relative;
    margin-bottom: 10px;
  }

  .grid figcaption {
    position: absolute;
    width: 90% !important;
    top: 0;
    left: 0;
    padding: 10px;
    min-height: 100% !important;
  }
  .grid figcaption p {
    font-size: 14px;
    text-align: left;
    margin-left: 3px;
    padding: 0;
    margin-top: 3px;
    padding-bottom: 3px;
    font-family: "Lato", sans-serif;
  }
  .grid figcaption h3 {
    margin-top: 1rem;
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 0;
    font-family: "Lato", sans-serif;
  }

  .grid figcaption span {
    margin-bottom: 5px;
    font-size: 14px;
    font-family: "Lato", sans-serif;
  }
  .grid figcaption img {
    width: 30px;
    margin-right: 5px;
    margin-left: 5px;
    margin-bottom: 10px;
  }
}
