.nav {
  background-color: white;
}

.nav-logo {
  height: 150px;
  left: 1rem;
  cursor: pointer;
  padding-bottom: 2rem;
}

.nav-logo:hover {
  cursor: pointer;
}

.nav .nav-content {
  display: flex;
  padding-left: 3%;
  padding-right: 3%;
  align-items: center;
  height: 100%;
}
.nav-links {
  justify-content: right;
}

.icons img {
  transition: all 0.3s ease 0s;
}

.icons img:hover {
  filter: brightness(0.6);
}

.icon-menu {
  height: 50px;
  padding: 6px;
}
