/* .nav-item a:hover {
  color: white;
} */
/* Individual item */
.bm-item {
  display: inline-block;
  /* Our sidebar item styling */
  text-decoration: none;
  /* margin-bottom: 10px; */
  color: #d1d1d1;
  transition: color 0.2s;
  outline: none;
  padding-bottom: 1rem;
  padding-left: 1rem;
}

/* Change color on hover */
.bm-item:hover {
  color: white;
}

/* The rest copied directly from react-burger-menu docs */

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 40px;
  height: 30px;
  right: 1.5rem;
  top: 1.5rem;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #f0473e;
}

/* Position and sizing of clickable cross button */
.bm-cross button {
  height: 24px !important;
  width: 24px !important;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 1em 2em 0;
  font-size: 25px;
  right: inherit;
  height: 100%;
  box-sizing: border-box;
  overflow: visible;
  position: fixed;
  right: 0px;
  width: 210px !important;
  white-space: nowrap;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  width: 120px;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

@media (min-width: 320px) and (max-width: 768px) {
  .nav-logo {
    height: 150px;
    top: 1rem;
    left: 0.5rem;
    cursor: pointer;
  }
  .bm-menu {
    background: #373a47;
    padding: 2em 2em 0;
    font-size: 25px;
    right: inherit;
  }
  /* Position and sizing of burger button */
  .bm-burger-button {
    position: fixed;
    width: 35px;
    height: 25px;
    /* right: 1.5rem;
    top: 1.5rem; */
  }
}

@import url(https://fonts.googleapis.com/css?family=Raleway:400, 700, 800);
*,
*:after,
*:before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.link {
  color: #cdeae8;
  text-decoration: none;
}
.link:hover,
.link:focus {
  color: #c94e50;
  cursor: pointer;
  text-decoration: none;
}

h1 {
  font-weight: 800;
  font-size: 3.75em;
}
.description {
  max-width: 20em;
  margin: 1.2em auto 1em;
  line-height: 1.3em;
  font-weight: 400;
  text-decoration: none;
}
.link button {
  font-size: 2em;
  max-width: 1200px;
  margin: 2em auto 3em;
  padding: 0 10em;
  text-decoration: none;
}
.link button {
  display: inline-block;
  margin: 0.75em;
  padding: 1.35em 1.1em;
  width: 15em;
  background: #cdeae8;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 800;
  border-top-left-radius: 20px 50px;
  border-top-right-radius: 20px 50px;
  border-bottom-right-radius: 20px 50px;
  border-bottom-left-radius: 20px 50px;
  cursor: pointer;
  text-decoration: none;
}
.current-link {
  background: #c94e50;
  color: #fffce1;
  text-decoration: none;
}
